@import './../mixins.scss';

.swiper{
    margin: 0;
    .swiper-button-prev,
    .swiper-button-next{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        margin: 0px;
        width: 38px;
        height: 38px;
        border: none;
        border-radius: 50%;
        background: linear-gradient(90deg, #8176AF 0%, #C0B7E8 100%);
        box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 0px 10px;
        --swiper-navigation-size: 20px;
        --swiper-theme-color: #433D60;
    }

    @include respond-to(small) {
        .swiper-slide{
            display: flex;
            justify-content: center;
            align-items: center;
            transform: translate3d(0px, 0px, 0px);
        }
        
        .contact{
            display: flex;
            align-items: center;
            justify-content: center;
            column-gap: 20px;
            min-width: 100%;
            height: 110px;
            color: #fff;
            border-radius: 90px;
            background: radial-gradient(50% 2900.76% at 50% 53.89%, rgba(58, 52, 86, 0.95) 0%, #211E2E 100%);
            box-shadow: 0px 4px 4px rgba(192, 183, 232, 0.01);

            h3{
                font-weight: 700;
                font-size: 17px;
            }
            p{
                margin-top: 5px;
                font-weight: 400;
                font-size: 14px;
                line-height: 17px;
                width: 200px;
            }
            &.phone{
                p{
                    width: 100px;
                }
            }
        }
    
        .service{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 320px;
            height: 511px;
    
            margin: 0 auto;
            align-items: center;
            background: radial-gradient(50% 50% at 50% 50%, #433D60 0%, #211E2E 100%);
            border-radius: 40px;
            padding: 27px 37px;
            &__img{
                width: 190px;
                height: 190px;
                border-radius: 50%;
                box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 0px 12px;
            }
            &__title{
                font-weight: 700;
                font-size: 24px;
                line-height: 29px;
            }
            &__description{
                font-weight: 400;
                font-size: 12px;
                line-height: 15px;
            }
            &__button{
                padding: 16px 40px;

                height: 48px;
                min-width: 100%;
                background: linear-gradient(90deg, #8176AF 0%, #C0B7E8 100%);
                border-radius: 40px;
                font-size: 12px;
                font-weight: 700;
                line-height: 15px;
                color: #343045;
                border: none;
                cursor: pointer;
            }
        }
    
        .brands__img{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 255px;
            height: 200px;
        }
    
        .cards{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            row-gap: 30px;
    
            &__elipse{
                margin-top: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 150px;
                height: 150px;
                background: linear-gradient(251.27deg, #C0B7E8 12.87%, #8176AF 89.33%);
                filter: drop-shadow(0px 0px 20px #000);
                box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 0px 19px;
                border-radius: 50%;
                font-weight: 700;
                font-size: 64px;
                line-height: 78px;
                color: #343045;
            }
            p{
                font-style: normal;
                font-weight: 700;
                font-size: 24px;
                line-height: 29px;
                text-align: center;
            } 
        }
    }

    @include respond-to(medium) {
        display: none;
    }

    @include respond-to(large) {
        display: none;
    }
}

